html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-facebook {
  width: 10px;
  height: 19px;
  background-image: url("../img/sprite.png");
  background-position: 0px 0px;
}

html, body {
  height: 100%;
}

body {
  min-width: 320px;
  font-size: 16px;
  font-family: "Tahoma";
  line-height: 1.4;
}

.page-wrapper {
  margin-bottom: -100px;
  min-height: 100%;
}

.page-wrapper:after {
  content: '';
  display: block;
  height: 100px;
}

.content {
  padding: 48px 0;
  border-top: 1px solid #232323;
  border-bottom: 1px solid #232323;
}

.video {
  z-index: 1;
  height: 480px;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.video:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  background-color: rgba(35, 132, 169, 0.4);
}

@media only screen and (max-width: 767px) {
  .video {
    height: 360px;
  }
}

@media only screen and (max-width: 599px) {
  .video {
    height: 280px;
  }
}

.video video {
  height: auto;
  width: auto;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 50%;
  z-index: -100;
  left: 50%;
  transform: translate(-50%, -50%);
}

.video__title {
  font-size: 38px;
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  color: #fff;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 767px) {
  .video__title {
    font-size: 28px;
  }
}

.container {
  max-width: 960px;
  padding: 0 10px;
  margin: 0 auto;
}

.row {
  font-size: 0;
  margin-left: -10px;
  margin-right: -10px;
}

.col {
  display: inline-block;
  vertical-align: top;
  padding: 0 20px;
  width: 50%;
}

@media only screen and (max-width: 767px) {
  .col {
    width: 100%;
    padding: 0 16px;
  }
}

@media only screen and (max-width: 599px) {
  .col {
    padding: 0 8px;
  }
}

.text {
  text-align: justify;
}

@media only screen and (max-width: 767px) {
  .text {
    text-align: center;
    margin-bottom: 48px;
  }
}

p {
  font-size: 16px;
}

.footer {
  background-color: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.footer p {
  font-size: 14px;
}

.form {
  width: 100%;
  margin: 0 auto;
}

@media only screen and (max-width: 767px) {
  .form {
    max-width: 599px;
  }
}

@media only screen and (max-width: 599px) {
  .form {
    max-width: 480px;
  }
}

.form-field:after {
  content: " ";
  display: table;
  clear: both;
}

.form-field:not(:last-child) {
  margin-bottom: 24px;
}

.form .label {
  font-size: 18px;
  font-family: "Tahoma";
  margin-bottom: 6px;
  display: block;
  line-height: 1;
}

.form .btn {
  display: inline-block;
  max-width: 180px;
}

@media only screen and (max-width: 599px) {
  .form .btn {
    display: block;
    margin: 0 auto;
  }
}

@media only screen and (max-width: 480px) {
  .form .btn {
    max-width: 100%;
  }
}

.input {
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  border: 1px solid #232323;
  width: 100%;
  height: 40px;
  color: #232323;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  transition: border-color 0.24s ease-in-out;
}

.input::placeholder {
  color: #232323;
}

.input:focus {
  border-color: #11d211;
}

.input:disabled {
  opacity: 0.7;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  border: 1px solid #232323;
  width: 100%;
  height: 40px;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  background-color: #2384a9;
  transition: 0.24s ease-in-out;
  position: relative;
}

.btn:active {
  top: 1px;
}

.btn:hover {
  background-color: #c1c1c1;
}

.btn:disabled {
  opacity: 0.7;
}
