$black: 											#000;
$black-lighten: 				#232323;
$white: 										#fff;
$red: 												#ff5555;
$gray: 											#f4f4f4;
$green: 										#11d211;

html, body {
	height: 100%; }

body {
	min-width: 320px;
	font-size: 16px;
	font-family: "Tahoma";
	line-height: 1.4; }

.page-wrapper {
	margin-bottom: -100px;
	min-height: 100%;
	&:after {
		content: '';
		display: block;
		height: 100px; } }

.content {
	padding: 48px 0;
	border-top: 1px solid $black-lighten;
	border-bottom: 1px solid $black-lighten; }

.video {
	z-index: 1;
	height: 480px;
	width: 100%;
	position: relative;
	overflow: hidden;
	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 0;
		background-color: rgba(35, 132, 169, 0.40); }
	@include sm {
		height: 360px; }
	@include xs {
		height: 280px; }
	video {
		height: auto;
		width: auto;
		min-width: 100%;
		min-height: 100%;
		position: absolute;
		top: 50%;
		z-index: -100;
		left: 50%;
		transform: translate(-50%, -50%); }
	&__title {
			font-size: 38px;
			position: absolute;
			top: 50%;
			left: 50%;
			text-align: center;
			color: $white;
			transform: translate(-50%, -50%);
			@include sm {
					font-size: 28px; } } }

.container {
	max-width: 960px;
	padding: 0 10px;
	margin: 0 auto; }

.row {
	font-size: 0;
	margin-left: -10px;
	margin-right: -10px; }

.col {
	display: inline-block;
	vertical-align: top;
	padding: 0 20px;
	width: 50%;
	@include sm {
		width: 100%;
		padding: 0 16px; }
	@include xs {
		padding: 0 8px; } }

.text {
	text-align: justify;
	@include sm {
		text-align: center;
		margin-bottom: 48px; } }
p {
	font-size: 16px; }

.footer {
	background-color: #f4f4f4;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100px;
	p {
		font-size: 14px; } }


.form {
	width: 100%;
	margin: 0 auto;
	@include sm {
		max-width: 599px; }
	@include xs {
		max-width: 480px; }
	&-field {
		@include clr;
		&:not(:last-child) {
			margin-bottom: 24px; } }
	.label {
		font-size: 18px;
		font-family: "Tahoma";
		margin-bottom: 6px;
		display: block;
		line-height: 1; }
	.btn {
		display: inline-block;
		max-width: 180px;
		@include xs {
			display: block;
			margin: 0 auto; }
		@include r(480) {
			max-width: 100%; } } }

.input {
	@include placeholder-color($black-lighten);
	display: inline-block;
	vertical-align: top;
	padding: 0 15px;
	border: 1px solid $black-lighten;
	width: 100%;
	height: 40px;
	color: $black-lighten;
	font-size: 16px;
	font-weight: 400;
	line-height: normal;
	transition: border-color 0.24s ease-in-out;
	&:focus {
		border-color: $green; }
	&:disabled {
		opacity: 0.7; } }

.btn {
	display: inline-block;
	vertical-align: top;
	padding: 0 15px;
	border: 1px solid $black-lighten;
	width: 100%;
	height: 40px;
	color: $white;
	font-size: 16px;
	font-weight: 400;
	line-height: normal;
	background-color: #2384a9;
	transition: 0.24s ease-in-out;
	position: relative;
	&:active {
		top: 1px; }
	&:hover {
		background-color: darken($gray, 20); }
	&:disabled {
		opacity: 0.7; } }
